<template>
  <div class="wrapper" :class="isPC ? 'pc' : 'h5'">
    <h1>Alchemy Toys Value Query</h1>
    <div class="form">
      <van-field name="check" label="">
        <template #input>
          <van-radio-group direction="horizontal" v-model="level">
            <van-radio :name="1">1</van-radio>
            <van-radio :name="2">2</van-radio>
            <van-radio name="3">3</van-radio>
            <van-radio name="4">4</van-radio>
            <van-radio name="5">5</van-radio>
            <van-radio name="6">6</van-radio>
            <van-radio name="7">7</van-radio>
            <van-radio name="8">8</van-radio>
          </van-radio-group>
        </template>
      </van-field>
      <van-field v-model="turnFee" label="(BNB)" />
      <van-field error :value="bnbValue.toFixed(6)" label="(BNB)" />
      <!-- <div class="btn-wrapper">
        <van-button type="primary" @click="calc"></van-button>
      </div> -->
    </div>
    <h1></h1>
    <div>
      <div class="input-group">
        <van-field
          type="digit"
          placeholder=""
          v-model.number="enlightenment.no1"
          label="01"
        />
        <van-field
          type="digit"
          placeholder=""
          v-model.number="enlightenment.no2"
          label="02"
        />
        <van-field
          type="digit"
          placeholder=""
          v-model.number="enlightenment.no3"
          label="03"
        />
        <van-field
          type="digit"
          placeholder=""
          v-model.number="enlightenment.no4"
          label="04"
        />
        <van-field
          type="digit"
          placeholder=""
          v-model.number="enlightenment.no5"
          label="05"
        />
        <van-field
          type="digit"
          placeholder=""
          v-model.number="enlightenment.no6"
          label="06"
        />
        <van-field
          type="digit"
          placeholder=""
          v-model.number="enlightenment.no7"
          label="07"
        />

        <van-field
          type="number"
          placeholder="BNB"
          v-model.number="enlightenment.jackpot"
          label=""
        />
        <!-- <van-field
          placeholder="BNB"
          :value="result"
          readonly
          disabled
          error
          label="/"
        /> -->
      </div>
      <div class="result">
        <span
          >1<span class="red">{{
            this.enlightReward.no1.toFixed(2)
          }}</span>
          BNB</span
        >
        <br /><span
          >2<span class="red">{{
            this.enlightReward.no2.toFixed(2)
          }}</span>
          BNB</span
        >
        <br /><span
          >3<span class="red">{{
            this.enlightReward.no3.toFixed(2)
          }}</span>
          BNB</span
        >
        <br /><span
          >4<span class="red">{{
            this.enlightReward.no4.toFixed(2)
          }}</span>
          BNB</span
        >
        <br /><span
          >5<span class="red">{{
            this.enlightReward.no5.toFixed(2)
          }}</span>
          BNB</span
        >
        <br /><span
          >6<span class="red">{{
            this.enlightReward.no6.toFixed(2)
          }}</span>
          BNB</span
        >
        <br /><span
          >7<span class="red">{{
            this.enlightReward.no7.toFixed(2)
          }}</span>
          BNB</span
        >
      </div>
      <div style="padding-left: 120px">
        <van-button @click="calcReward" type="primary"></van-button>
      </div>
    </div>
  </div>
</template>
<script>
import Vue from 'vue'
import { RadioGroup, Radio, Field } from 'vant'
import { isPC } from '@/assets/js/utils'

Vue.use(Radio)
Vue.use(RadioGroup)
Vue.use(Field)

export default {
  data() {
    return {
      level: '',
      turnFee: 0.011543, //
      bnbValue: 0,
      enlightenment: {
        no1: '',
        no2: '',
        no3: '',
        no4: '',
        no5: '',
        no6: '',
        no7: '',
        jackpot: '',
      },
      enlightReward: {
        no1: 0,
        no2: 0,
        no3: 0,
        no4: 0,
        no5: 0,
        no6: 0,
        no7: 0,
      },
    }
  },
  computed: {
    isPC() {
      return isPC()
    },
    result() {
      return `
      1${this.enlightReward.no1.toFixed(2)} BNB,
      2${this.enlightReward.no2.toFixed(2)} BNB,
      3${this.enlightReward.no3.toFixed(2)} BNB,
      4${this.enlightReward.no4.toFixed(2)} BNB,
      5${this.enlightReward.no5.toFixed(2)} BNB,
      6${this.enlightReward.no6.toFixed(2)} BNB,
      7${this.enlightReward.no7.toFixed(2)} BNB,
      `
    },
  },
  methods: {
    calcReward() {
      const { no1, no2, no3, no4, no5, no6, no7, jackpot } = this.enlightenment
      let enlightList = [no1, no2, no3, no4, no5, no6, no7]
      // 
      let totalScore = 0
      enlightList.forEach((num) => {
        if (Number(num)) {
          totalScore += Number(num)
        }
      })
      // 
      let totalPrecent = 0
      enlightList.forEach((num) => {
        if (Number(num)) {
          totalPrecent += totalScore / Number(num)
        }
      })
      // 
      this.enlightReward.no1 = no1
        ? (totalScore / Number(no1) / totalPrecent) * jackpot
        : 0
      this.enlightReward.no2 = no2
        ? (totalScore / Number(no2) / totalPrecent) * jackpot
        : 0
      this.enlightReward.no3 = no3
        ? (totalScore / Number(no3) / totalPrecent) * jackpot
        : 0
      this.enlightReward.no4 = no4
        ? (totalScore / Number(no4) / totalPrecent) * jackpot
        : 0
      this.enlightReward.no5 = no5
        ? (totalScore / Number(no5) / totalPrecent) * jackpot
        : 0
      this.enlightReward.no6 = no6
        ? (totalScore / Number(no6) / totalPrecent) * jackpot
        : 0
      this.enlightReward.no7 = no7
        ? (totalScore / Number(no7) / totalPrecent) * jackpot
        : 0
    },
    calc() {
      let { level } = this
      level = Number(level)
      if (!level) return
      let bnbValue = 0
      let turnFee = parseFloat(this.turnFee)
      let level1Value = this.getLevel1Value({ turnFee })
      let level2Value = level1Value * 2 + turnFee
      let level3Value = level2Value * 2 + turnFee
      let level4Value = level3Value * 2 + turnFee
      let level5Value = level4Value * 2 + turnFee
      let level6Value = level5Value * 2 + turnFee
      let level7Value = level6Value * 2 + turnFee
      let level8Value = level7Value * 2 + turnFee
      if (level === 1) {
        bnbValue = level1Value
      }
      if (level === 2) {
        bnbValue = level2Value
      }
      if (level === 3) {
        bnbValue = level3Value
      }
      if (level === 4) {
        bnbValue = level4Value
      }
      if (level === 5) {
        bnbValue = level5Value
      }
      if (level === 6) {
        bnbValue = level6Value
      }
      if (level === 7) {
        bnbValue = level7Value
      }
      if (level === 8) {
        bnbValue = level8Value
      }
      this.bnbValue = bnbValue
    },
    getLevel1Value({ turnFee }) {
      return turnFee / 3
    },
  },
  watch: {
    level(val) {
      if (val) {
        this.calc()
      }
    },
  },
}
</script>
<style lang="stylus" scoped>
.wrapper {
  // width 1260px
  margin 20px auto
  &.pc {
    width 1260px
    margin 50px auto
  }
  h1 {
    text-align center
  }
  .van-cell {
    background transparent
  }
  .btn-wrapper {
    padding-left 110px
    padding-top 20px
  }
  .result {
    line-height 1.8
    margin 10px 0
    padding-left 15px
    font-size 14px
  }
}
.red {
  color red
}
.van-radio--horizontal {
  margin-bottom 12px
